import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import './main.css';

class App extends Component {
	
	state = {
		displayed: false,
		answerBinary: null,
		answerOctal: null,
		answerDeciaml: null,
		answerHex: null,
	}
	
	componentDidMount() {
		alert('Hi there this is a very simple number converter application build using react. inspiration Traversy Media number converter with HTML,BOOTSTRAP,JS. im Leul z Developer. gethub  @Leul-dev')
	}
	
	calculate = (i) => {
		let input = +i;
		if(input !== null) {
			this.setState({displayed: true});
		}else {
			this.setState({displayed: false});
		}

		console.log(input)
		this.setState({answerBinary: input.toString(2)});
		this.setState({answerOctal: input.toString(8)});
		this.setState({answerDeciaml: input.toString(10)});
		this.setState({answerHex: input.toString(16)});
		
	}
	
	
	render() {
		return (
			<div className="container">
				<Numinput calculate={this.calculate} />
				<Displaycard theme='orange' 
				imgLabel="01"
				title="Binary"
				ans={this.state.answerBinary}
				displayed={this.state.displayed}
				/>
				<Displaycard theme="green" 
				imgLabel="8"
				title="Octal"
				ans={this.state.answerOctal}
				displayed={this.state.displayed}
				/>
				<Displaycard theme="blue"  
				imgLabel="10"
				title="Decimal"
				ans={this.state.answerDeciaml}
				displayed={this.state.displayed}
				/>
				<Displaycard theme='red' 
				imgLabel="16" 
				title="Hexa-Decimal"
				ans={this.state.answerHex}
				displayed={this.state.displayed}
				/>
			</div>
		);
	}
}

class Numinput extends Component {
	constructor(props) {
		super(props);
		this.state = { value: '' }
		this.onChange = this.onChange.bind(this);
	}
	
	onChange = (e) => {
		this.setState({ value: e.target.value })
		this.props.calculate(e.target.value);
	}
	
	render() {
		return (
			<div>
				<input type="number" onChange={this.onChange} 
				value={this.state.value}
				style={this.input_style}
				placeholder="Enter any Number...."
				autoFocus 
				/>
			</div>
		);
	}
	
	input_style = {
		display: 'block',
		background: '#fff',
		border: 'none',
		borderRadius: '2px',
		boxShadow: 'inset 0px 1px 2px rgba(0,0,0,0.4)',
		padding: '0px 1em',
		width: '100%',
		height: '35px',
		fontFamily: 'Verdana',
		fontSize: '0.8em',
		outline: 'none',
		margin: '3em auto'
	};
}

class Displaycard extends Component {
	
	theme = 'card ';
	img = 'img ';
	
	themeHandler = () => {
		let theme_var = this.props.theme;
		
		if(theme_var === 'red') {
			this.theme += 'red'; this.img += ' green';
		}
		else if(theme_var === 'blue') {
			this.theme += 'blue'; this.img += ' orange';
		}
		else if(theme_var === 'green') {
			this.theme += 'green'; this.img += ' red';
		}
		else if(theme_var === 'orange') {
			this.theme += 'orange'; this.img += ' blue';
		}
		else {
			this.theme += ''; this.img += '';
		}
		
		if(this.props.displayed === true) {
			this.theme += ' display';
		} else {
			this.theme += ' hide';
		}
	}
	
	render() {
		
		this.themeHandler();
		return (
			<div className={this.theme}>
				<div className={this.img}>{this.props.imgLabel}</div>
				<div className="title">{this.props.title}</div>
				<div className="calc">{this.props.ans}</div>
			</div>
		);
	}
}

ReactDOM.render(<App />, document.getElementById('root'));